import { FunctionComponent } from 'preact';
import { useMemo } from 'preact/hooks';
import register from 'preact-custom-element';

type ComplexityLevel = 'low' | 'medium' | 'high';

interface ProjectItemProps {
  title?: string;
  complexity?: ComplexityLevel;
  description?: string;
}

// CSS styles as a string
const styles = `
  <style>
    .project-item-container {
      display: block;
      margin-bottom: 1.5rem;
    }

    .project-card {
      background: white;
      border: 1px solid #e5e7eb;
      border-radius: 0.75rem;
      padding: 1.5rem;
      box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
      transition: all 0.2s ease;
      position: relative;
      height: 100%;
    }

    .project-card:hover {
      box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
      transform: translateY(-2px);
    }

    .complexity-badge {
      position: absolute;
      top: 1rem;
      right: 1rem;
      padding: 0.25rem 0.75rem;
      border-radius: 9999px;
      font-size: 0.75rem;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 0.05em;
    }

    .complexity-low {
      background: #dcfce7;
      color: #166534;
    }

    .complexity-medium {
      background: #fef3c7;
      color: #92400e;
    }

    .complexity-high {
      background: #fee2e2;
      color: #991b1b;
    }

    .project-title {
      font-size: 1.25rem;
      font-weight: 700;
      color: #111827;
      margin: 0 0 0.75rem 0;
      line-height: 1.4;
      padding-right: 4rem;
    }

    .project-description {
      color: #6b7280;
      line-height: 1.6;
      margin: 0;
    }

    @media (min-width: 768px) {
      .project-item-container {
        display: inline-block;
        vertical-align: top;
      }
    }

    @media (min-width: 1024px) {
      .project-item-container {
      }
    }
  </style>
`;

/**
 * Project Item component displaying project information with complexity badge
 * 
 * @param title - Project title
 * @param complexity - Project complexity level (low, medium, high)
 * @param description - Project description
 */
const ProjectItem: FunctionComponent<ProjectItemProps> = ({
  title = '',
  complexity = 'medium',
  description = ''
}) => {
  const badgeClass = useMemo(() => {
    return `complexity-badge complexity-${complexity}`;
  }, [complexity]);

  return (
    <div class="project-item-container">
      <div
        dangerouslySetInnerHTML={{ __html: styles }}
      />
      <div class="project-card">
        <div class={badgeClass}>
          {complexity}
        </div>
        <h3 class="project-title">{title}</h3>
        <p class="project-description">{description}</p>
      </div>
    </div>
  );
};

register(ProjectItem, 'project-item', ['title', 'complexity', 'description']);

export { ProjectItem, type ProjectItemProps, type ComplexityLevel };
